*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  background: var(--background);
  height: 100vh;
  width: 100vw;
  font-family: "Poppins", sans-serif;
}

body {
  display: flex;
  justify-content: center;
  align-items: center;
}

img {
  max-width: 100%;
  border-radius: 0.5em;
}

@media (min-width: 900px) {
  svg {
    margin-top: 2em;
  }
}

:root {
  --background: #2d2e36;
  --text-header: #fff;
  --text-color: #8d91a6;
}

h1 {
  margin-top: 0;
  font-size: 1.875rem;
  color: var(--text-header);
  text-transform: capitalize;
  font-weight: 400;
}

p {
  color: var(--text-color);
  line-height: 1.5;
}

.container {
  background: #383a45;
  max-width: 80%;
  min-height: 70%;
  max-height: 80%;
  border-radius: 0.5em;
  padding: 2em;
}
@media (min-width: 900px) {
  .container {
    display: grid;
    grid-gap: 1.5em;
    height: 400px;
    max-width: 730px;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: 1fr;
    align-items: center;
    padding: 1em;
  }
}
.container__image, .container__body {
  padding: 1em;
}
@media (min-width: 1900px) {
  .container__image, .container__body {
    padding: 2em;
  }
}